import React from 'react';
import './QuemSomos_Texts.css'; // Make sure to create this CSS file

const QuemSomos_Texts = () => {
    return (
        <div >
            <p className="QS-text1">O termo 'Teca', derivado do Grego TKHE, significando 'depsito' ou 'lugar de guarda',  O sufixo 'Teca' destaca a ideia de conjunto e coleo, sublinhando a essncia da Registroteca, como um hub confivel para aglutinar servios, conhecimento e contedo relacionados ao Direito, ao Empreendedorismo e  Inovao.
            </p>
            <p className="QS-text2">Com essa inspirao, a Registroteca surge como a materializao dessa unio poderosa. Somos mais do que uma plataforma digital; somos um ponto de encontro dinmico e confivel para aqueles que buscam explorar as intersees entre o direito, o empreendedorismo e a inovao.
            </p>
            <p className="QS-text3">Na essncia do termo 'Teca', enxergamos no apenas um hub, mas um espao dinmico de conexes e oportunidades. Nosso compromisso  aglutinar servios, conhecimentos e contedos de alta relevncia em um nico hub confivel. Aqui, proporcionamos um ambiente que vai alm do convencional, promovendo a convergncia de ideias, estratgias jurdicas e iniciativas empreendedoras.
            </p>
            <p className="QS-text4">Acreditamos que a verdadeira inovao surge quando o conhecimento jurdico se entrelaa com a paixo empreendedora.  nesse ponto de encontro que catalisamos o potencial transformador de cada ideia, protegendo-a e impulsionando-a para o prximo nvel.
            </p>
            <p className="QS-text5">Seja voc um profissional do direito, um visionrio empreendedor ou um entusiasta da inovao, a Registroteca  a sua plataforma de referncia. Estamos aqui para fornecer no apenas servios de alta qualidade, mas para construir uma comunidade vibrante e colaborativa. Juntos, exploramos  novas fronteiras, protegemos ideias valiosas e catalisamos o progresso em um mundo onde o direito, empreendedorismo e inovao se encontram.
            </p>
        </div>
    );
};

export default QuemSomos_Texts;