import React, { useState } from 'react';
import './ContactForm.css'; // Ensure the path matches where your CSS file is located

const ContactForm = () => {
    // Array for all 9 services to be displayed
    const services = Array.from({ length: 9 }, (_, i) => `Service ${i + 1}`);
    // Indices for 3 rows
    const rows = [[0, 1, 2], [3, 4, 5], [6, 7, 8]];
    // State to track toggle status of each button
    const [toggled, setToggled] = useState(Array(9).fill(false));

    // Function to handle button toggle
    const handleToggle = (index) => {
        const newToggled = [...toggled];
        newToggled[index] = !newToggled[index];
        setToggled(newToggled);
    };

    return (
        <div className="custom-contact-form-container">
            <div className="custom-form-inputs">
                <input type="text" placeholder="Your Name" />
                <input type="email" placeholder="Your Email" />
                <textarea placeholder="Additional Information"></textarea>
            </div>
            <div className="custom-buttons-containerC">
                {rows.map((row, rowIndex) => (
                    <div key={rowIndex} className="custom-buttons-rowC">
                        {row.map(index => (
                            <button
                                key={index}
                                className={`custom-buttonC ${toggled[index] ? 'custom-buttonC-toggled' : ''}`}
                                onClick={() => handleToggle(index)}
                            >
                                {services[index]}
                            </button>
                        ))}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default ContactForm;