import React, { useState } from 'react';
import './ImageRoulette.css'; // Make sure to link the CSS file for styling

const ImageRoulette = ({ images, activeImageCount}) => {
    const [currentIndex, setCurrentIndex] = useState(0);

    const handleNext = () => {
        setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
    };

    const handlePrev = () => {
        setCurrentIndex((prevIndex) => (prevIndex - 1 + images.length) % images.length);
    };

    // Function to get the images to display
    const getImagesToDisplay = () => {
        let imagesToDisplay = [];
        for (let i = 0; i < activeImageCount; i++) {
            let index = (currentIndex + i) % images.length;
            imagesToDisplay.push(images[index]);
        }
        return imagesToDisplay;
    };

    return (
        <div className="carousel-container">
            <button onClick={handlePrev} className="carousel-arrow carousel-left-arrow">{'<'}</button>
            <div className="carousel-images-container">
                {getImagesToDisplay().map((image, index) => (
                    <img key={index} src={image.src} alt={image.alt} className="carousel-image" style={{ width: "200px", height: "125px" }} />
                ))}
            </div>
            <button onClick={handleNext} className="carousel-arrow carousel-right-arrow">{'>'}</button>
        </div>
    );
};

export default ImageRoulette;