import React from 'react';
import './FullScreenImageWithText.css'; // Make sure to create this CSS file

const EntryImage = ({ imageUrl, headerText }) => {
  return (
    <div className="fullscreen-image-container" style={{ backgroundImage: `url(${imageUrl})` }}>
      <div className="gradient-overlay">
        <h1 className="centered-text">{headerText}</h1>
      </div>
    </div>
  );
};

export default EntryImage;