import React from 'react';
import './TripleImageWithCircle.css'; // Ensure this is correctly linked

const TripleImageWithCircle = ({ images, circlePosition1, circlePosition2, circlePosition3, circlePosition4, circlePosition5,  imageSpacing, imagesPosition }) => {
    return (
        <div className="parent-container" style={{ ...imagesPosition }}>
            <div className="circleSecond" style={{ ...circlePosition2 }}></div>
            <div className="circleThird" style={{ ...circlePosition3 }}></div>

            <div className="circle" style={{ ...circlePosition1 }}></div>
            <div className="images-container" >
                {images.map((image, index) => (
                    <img
                        key={index}
                        src={image.src}
                        alt={image.alt}
                        style={{ marginBottom: `${index < images.length - 1 ? imageSpacing : 0}px` }}
                        className={`rounded-image${index}`}
                    />
                ))}
            </div>
        </div>
    );
};

export default TripleImageWithCircle;