import React from 'react';
import './ImageContainers.css'; // Ensure this is correctly linked


const images = [
    { src: process.env.PUBLIC_URL + '/bookTopView.jpg', alt: 'Image 1' },
    { src: process.env.PUBLIC_URL + '/bookTopView.jpg', alt: 'Image 2' },
    { src: process.env.PUBLIC_URL + '/bookTopView.jpg', alt: 'Image 3' },
];


const ImageContainers = ({ imagesPosition }) => {
    return (
        <div >
            <div className="QS-images-container" >
                {images.map((image, index) => (
                    <img
                        key={index}
                        src={image.src}
                        alt={image.alt}
                        className={`quem-somos-image${index}`}
                    />
                ))}
            </div>
        </div>
    );
};

export default ImageContainers;