import React from 'react';
import './Example.css';



const Example = ({ imageUrl, text, customTop }) => {
    return (
        <div>
            <div className="container" style={{ top: customTop }}>
                <p className="text">{text}</p>
                <div className="image" style={{ backgroundImage: `url(${imageUrl})` }}>
                </div>
                <p className="text2">Venha comecar a sua jornada conosco!</p>
                <p className="text3">Nunca foi tao facil ter acompanhamento legal para seu proprio negocio.</p>
                <p className="text4">Conheca nossas marcas parceiras</p>
                <p className="text5">Venha fazer parte do nosso time!</p>
                <p className="text6">Nossos servicos:</p>
            </div>
        </div>
    );
};

export default Example;